img {
    width: 100vw;
    -webkit-user-drag: none;
    -khtml-user-drag: none;
    -moz-user-drag: none;
    -o-user-drag: none;
}

.header-wrapper {
    padding-top: 50px;
    text-align: center;
}
.card {
    margin: 18px 25px 18px 25px;
    border: 5px solid #F1F1F1;
    box-shadow: 0px 3px 6px #888888;
    transition: all 150ms;
    -webkit-transition: all 150ms;
    height: 160px;
    width: 160px;
    cursor: pointer;
    -webkit-user-drag: none;
    -khtml-user-drag: none;
    -moz-user-drag: none;
    -o-user-drag: none;
    z-index: -1;

}

.card:hover {
    -webkit-transform: scale(1.10, 1.10);
            transform: scale(1.10, 1.10);
    box-shadow: 0px 10px 12px #888888;
}

.cardContainer {
    display: inline-block;
    text-align: center;
    padding: 0px 0px 50px 0px;
    max-width: 900px;
    min-width: 440px;
    z-index: 1;
}

.wrapper {
    margin-top: 15px;
    text-align: center;
    position: relative;
}

.shake {
    -webkit-animation: shake 0.82s cubic-bezier(.36, .07, .19, .97) both;
            animation: shake 0.82s cubic-bezier(.36, .07, .19, .97) both;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    -webkit-perspective: 1000px;
            perspective: 1000px;
}

@-webkit-keyframes shake {

    10%,
    90% {
        -webkit-transform: translate3d(-1px, 0, 0);
                transform: translate3d(-1px, 0, 0);
    }

    20%,
    80% {
        -webkit-transform: translate3d(2px, 0, 0);
                transform: translate3d(2px, 0, 0);
    }

    30%,
    50%,
    70% {
        -webkit-transform: translate3d(-4px, 0, 0);
                transform: translate3d(-4px, 0, 0);
    }

    40%,
    60% {
        -webkit-transform: translate3d(4px, 0, 0);
                transform: translate3d(4px, 0, 0);
    }
}

@keyframes shake {

    10%,
    90% {
        -webkit-transform: translate3d(-1px, 0, 0);
                transform: translate3d(-1px, 0, 0);
    }

    20%,
    80% {
        -webkit-transform: translate3d(2px, 0, 0);
                transform: translate3d(2px, 0, 0);
    }

    30%,
    50%,
    70% {
        -webkit-transform: translate3d(-4px, 0, 0);
                transform: translate3d(-4px, 0, 0);
    }

    40%,
    60% {
        -webkit-transform: translate3d(4px, 0, 0);
                transform: translate3d(4px, 0, 0);
    }
}
.score-board {
    background-image: url(/Click_Em_All/static/media/header-bg.31d606cc.jpg);
    position: fixed;
    top: 0;
    height: 65px;
    width: 100vw;
    background-size: 100%;
    box-shadow: 0 8px 12px #222222;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: nowrap;
            flex-wrap: nowrap;
    -webkit-justify-content: space-around;
            justify-content: space-around;
}

.split {
    white-space: nowrap;
    color: aliceblue;
    line-height: 65px;
    font-size: calc(8px + 1.5vw);
}

.split:nth-child(1) {
    font-weight: bold;
    font-size: calc(8px + 1.9vw);
}

.split:nth-child(2) {
    text-transform: capitalize;
    width: 33%;
}

.correct {
    -webkit-animation: greenGuess .5s;
    animation: greenGuess .5s;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
}

.incorrect {
    -webkit-animation: redGuess .5s;
    animation: redGuess .5s;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
}

@-webkit-keyframes greenGuess {
    0% {
        color: white;
    }

    50% {
        color: green;
    }

    100% {
        color: white;
    }
}

@keyframes greenGuess {
    0% {
        color: white;
    }

    50% {
        color: green;
    }

    100% {
        color: white;
    }
}

@-webkit-keyframes redGuess {
    0% {
        color: white;
    }

    50% {
        color: red;
    }

    100% {
        color: white;
    }
}

@keyframes redGuess {
    0% {
        color: white;
    }

    50% {
        color: red;
    }

    100% {
        color: white;
    }
}
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
margin: 0;
padding: 0;
border: 0;
font-size: 100%;
font: inherit;
vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
display: block;
}
body {
line-height: 1;
overflow-x: hidden;
}
ol, ul {
list-style: none;
}
blockquote, q {
quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
content: '';
content: none;
}
table {
border-collapse: collapse;
border-spacing: 0;
}
